// CONTACT SECTION START
#contact {
    background: #3d3a3a; // Contactn arxa plan qara - DYLCK !
    padding-top: 10px;
    padding-bottom: 10px;
    color: #FFFFFF; // Contactdak yazlar a rngd - DYL BLR !

    a {
        color: #FFFFFF;
    }

    li{                  // email(contact) v social iconlara 5 padding verilib
      padding-top: 5px;
      padding-bottom: 5px;
      a{
        padding-top: 5px;
        padding-bottom: 5px;
      }
    }

    // SOCIAL ICONS START
    // Hover olunduu zaman hr icon z rngini alr v arxa plan a olur - DYL BLR !
    ul:nth-child(2){
      i{
        transition: all .3s ease;
      }

      li:nth-child(1){   // facebook
        a{
          padding-left: 10px;
          padding-right: 10px;
        }
      }
      li:nth-child(2){    //twitter
        a{
          padding-left: 8px;
          padding-right: 8px;
        }
      }
      li:nth-child(3){    //google +
        a{
          padding-left: 5px;
          padding-right: 5px;
        }
      }

      li:nth-child(1):hover {   // Facebook hover
          //background: #FFFFFF;
          i {
              color: #0374a0;
              transform: scale(2);
          }
      }
      li:nth-child(2):hover {   // Twitter hover
        //  background: #FFFFFF;
          i {
              color: #1DA1F2;
              transform: scale(2);
          }
      }
      li:nth-child(3):hover {  // Google + hover
          //background: #FFFFFF;
          i {
              color: #DB493C;
              transform: scale(2);
          }
      }
    }
    //SOCIAL ICONS END
}
// CONTACT SECTION END

// NAVBAR SECTION START
#navbar {
  padding-top: 20px;
    nav {
        border-radius: 0 !important;   // nav v navbar-defaultun bzi xsusiyytlri sfrlanb
        border: 0 !important;
        box-shadow: none;
        background: #FFFFFF;

    }
    .navbar-brand {
        margin-right: 30px;
        padding-top: 0; // navbar-brand'in top paddingi olduuna gr sfrlanb.
        img {
            width: inherit;
            height: inherit;
        }
    }
    #myNavbar{
      ul:first-child{     // Naviqasiya hisssi
        text-transform: uppercase;
        font-weight: bold;
        font-size: 13px;
        i{
          padding-right: 5px;
        }
        a{
          transition: all .3s ease;
        }
        a:hover{
          color: #2AB6FF !important;
        }
        .active{  // Naviqasiyada aktiv dyidirilib
          color: #2AB6FF;
          background: none;
        }
      }
      ul:last-child{    // Daxil ol v qeydiyyat hisssi
        a{
          transition: all .3s ease;
        }
        a:hover{
          color: #2AB6FF !important;
        }
        li:last-child{
          a{           // Google + iconunu v Daxil ol szn eyni sviyyy gtirmk n padding:5 verilib.
            padding-right: 10px !important;
          }
        }
        i{
          margin-right: 3px;
          color: #2AB6FF;
        }
      }
    }
}
// NAVBAR SECTION END
